import React, {useState, useEffect} from "react";
import './styles.css'
import { createUseStyles } from "react-jss";
import bg from '../imgs/tvr_Mesa de trabajo 1.png'
import Link from "gatsby-link";

const useStyles = createUseStyles({
    container: {
        display: 'flex',
        overflow: 'hidden',
        '@media (max-width: 620px)': {
            alignItems: 'center',
            flexDirection: 'column',
        },
    },
});

const App = () => {
    const [counter, setCounter] = useState(0);
    const classes = useStyles();
    
    useEffect(() => {
        let intervalId = setInterval(() => {
            if (counter >= 360) {
                return setCounter(0)
            }
            console.log(counter)
            
            setCounter(counter => counter + 1);
        }, 10);
        
        return () => clearInterval(intervalId);
    });
    
    return (
        <div className={classes.container}>
            <div style={{
                position: 'absolute',
                backgroundImage: `url("https://web.archive.org/web/20091026201200/http://br.geocities.com/alemdoceucinzento/spacemov.gif")`,
                overflow: 'hidden',
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                paddingBottom: 50,
                width:'100vw',
                backgroundSize: 'contain',
                backgroundPositionX: -10,
                backgroundPositionY: -10,
                zIndex: -1,
                color: 'deeppink',
                fontSize: '1rem',
                textAlign: 'center',
            }}
            >
                <img
                    src="http://www.gigaglitters.com/created/LZiIMmgX3Z.gif"
                    width="250"
                    height="67"
                    border="0"
                    alt="http://www.rasaint.net/ - Glitter Graphics" />
                
            </div>
        </div>
    );
}

export default App
